@import "./__logo/menu-button__logo";
@import "./__burger/menu-button__burger";
.menu-button {
	display: flex;
	flex-direction: column;
	align-items: center;
	&:hover {
		cursor: pointer;
		svg {
			animation: test 1s linear infinite;
		}
	}
}
@media only screen and (max-width: $mobile) {
	.menu-button {
		position: absolute;
		flex-direction: row;
		left: 1rem;
		top: 0;
	}
}
