.fresh-news__image {
	height: 9.9rem;
	width: 100%;
	background-color: $main-color;
	border-radius: 2px;
	margin-top: 0.8rem;
	margin-bottom: 1rem;
	order: 1;
}
@media only screen and (max-width: $tablet) {
	.fresh-news__image {
		float: left;
		width: 4.5rem;
		height: 4.5rem;
		margin-top: 0;
		margin-right: 1.1rem;
	}
}
