@import "../blocks/adress/adress";
@import "../blocks/social-link/social-link";
@import "../blocks/social-link/__item/social-link__item";

.footer {
	font-family: $inter;
	color: $main-grey;
	display: flex;
	flex-wrap: nowrap;
	padding: 3.5rem 1.2rem 2.1rem 2.2rem;
	background-color: $backcolor;
}
.footer__social-link {
	flex-basis: 10.8rem;
	flex-shrink: 0;
}
.footer__description {
	flex-grow: 1;
}

@media only screen and (max-width: $tablet) {
	.footer {
		padding-top: 1.7rem;
	}
}
@media only screen and (max-width: $mobile) {
	.footer {
		flex-wrap: wrap;
		padding-top: 3rem;
	}
	.footer__social-link {
		display: flex;
		justify-content: center;
		flex-basis: 100%;
	}
	.footer__description {
		margin-top: 2.3rem;
	}
}
