// stylelint-disable at-rule-empty-line-before

@import "variables";
@import "functions";
@import "mixins";
@import "sprites";
@import "fonts";
@import "vendor";
@import "base";
@import "helpers";

@import "components/sidePanel";
@import "components/pagination";
@import "components/userStat";
@import "components/news";
@import "components/price";
@import "components/soldShares";
@import "components/video";
@import "components/footer";

html {
	font-size: 13px;
	font-family: $inter;
}
body {
	background-color: $backcolor;
}
.container-main {
	max-width: $desktop;
	margin: 0 auto;
	.skip-menu {
		visibility: hidden;
	}
	.content--wrap {
		display: flex;
	}
	.content__main {
		width: 100%;
	}
	.content__head {
		font-family: $interSemi;
		font-weight: 400;
		// position: absolute;
		// transform: translateX(50%);
		// margin-left: 50%;
		// margin-right: 50%;
		text-align: center;
		visibility: hidden;
		width: 0;
		height: 1px;
		margin: 0;
	}
	.content__block {
		width: 100%;
	}
	.content__wrap-news {
		display: flex;
		padding-right: 1.6rem;
	}
	.content__news {
		width: 23.1rem;
		order: 2;
	}
	.content__cover-center-block {
		width: 100%;
		display: flex;
		flex-direction: column;
	}
	.content__forcast-shares {
		display: flex;
	}
}
.hover-tablet {
	width: 100%;
	height: 2000px;
	background-color: $black;
	opacity: 0.5;
	position: absolute;
	z-index: 5;
}
// ie-11 styles
.is-browser-ie-11 {
	.stat__link {
		position: inherit;
		margin-right: 1rem;
	}
	.stat__link--row {
		right: 1rem;
	}
	.video__item {
		width: auto;
	}
	.video__wrapper {
		position: inherit;
	}
	.content__wrap-news {
		overflow: hidden;
	}
	.video {
		overflow-x: hidden;
	}
	.video__header {
		position: absolute;
	}
	.video__header--all {
		position: absolute;
		right: 0;
	}
	.container-main {
		.open {
			width: 24.8rem;
			padding-right: 20px;
			.logo__picture {
				width: 48px;
				height: 48px;
			}
			.icon {
				width: 20px;
			}
		}
	}
	.user__img {
		&::after {
			width: 14px;
			height: 14px;
		}
	}
	.stat__percent {
		z-index: 5;
	}
	@media only screen and (max-width: $tablet) {
		.stat__descript {
			position: relative;
		}
		.stat__percent {
			position: absolute;
			transform: translateY(0%);
		}
	}
	@media only screen and (max-width: $mobile) {
		.container-main {
			.open {
				padding-right: 10px;
				.menu-button {
					position: relative;
					left: 0;
				}
				.logo__picture {
					width: 30px;
				}
				.logo__descript {
					right: 0;
				}
			}
		}
		.stat__link--row {
			right: inherit;
			left: inherit;
		}
		.stat__show {
			bottom: 2rem;
			left: 0.5rem;
		}
		.sold-shares__sold {
			padding-top: 0;
		}
		.sold-shares__sold-percent {
			bottom: -2rem;
		}
	}
}

// firefox styles
.is-browser-firefox {
	.container-main {
		.open {
			.account-navigation__item {
				padding-right: 8rem;
			}
		}
	}
	.price__wrap-image {
		position: relative;
		width: 100%;
		height: 268px;
	}
	.chart-main {
		width: 100%;
		position: absolute;
		top: 0;
	}
	.sold-shares {
		width: 35%;
	}
	@media only screen and (max-width: $tablet) {
		.sold-shares {
			width: 100%;
		}
		.stat__show {
			bottom: 0;
		}
		.container-main {
			.open {
				padding-right: 0;
			}
		}
	}
	@media only screen and (max-width: $mobile) {
		.sold-shares__sold {
			padding-top: 0;
		}
		.sold-shares__sold-percent {
			bottom: -2rem;
		}
	}
}

// style edge

.is-browser-edge {
	@media only screen and (max-width: $mobile) {
		.sold-shares__sold {
			padding-top: 0;
		}
		.sold-shares__sold-percent {
			bottom: -2rem;
		}
	}
}

// safari style

.is-browser-safari {
	.container-main {
		.open {
			width: 24.8rem;
			padding-right: 20px;
			.logo__picture {
				width: 48px;
				height: 48px;
			}
			.icon {
				width: 20px;
			}
		}
	}
	.price__wrap-image {
		position: relative;
		width: 100%;
		height: 268px;
	}
	.chart-main {
		width: 100%;
		position: absolute;
		top: 0;
	}
	.sold-shares {
		width: 35%;
	}
}

@media only screen and (max-width: $tablet) {
	.container-main {
		.content__wrap-news {
			flex-direction: column;
		}
		.content__news {
			order: -1;
			width: 100%;
		}
		.content__forcast-shares {
			flex-direction: column;
		}
	}
}
@media only screen and (max-width: $mobile) {
	.container-main {
		overflow: hidden;
		.content__wrap-news {
			padding-left: 0;
		}
		// .content__main {
		// 	overflow: hidden;
		// }
	}
}
