@import "./--count/side-panel__news--count";

.side-panel__news {
	display: flex;
	align-items: center;
	&::after {
		content: "";
		right: -2rem;
		position: absolute;
		// box-sizing: content-box;
		width: 1.3rem;
		height: 1.3rem;
		background-color: $pink;
		border-radius: 50%;
	}
}
