.stat__link--row {
	fill: $active-menu;
	position: absolute;
	right: -1rem;
	width: 8px;
	height: 8px;
	// .icon {
	// 	width: 8px;
	// 	height: 8px;
	// }
}
@media only screen and (max-width: $tablet) {
	.stat__link--row {
		visibility: visible;
	}
}
@media only screen and (max-width: $mobile) {
	.stat__link--row {
		left: 0;
		.show-more {
			width: 8px;
		}
	}
}
@media only screen and (max-width: 320px) {
	.stat__link--row {
		left: 0;
	}
}
